<template>
    <div>
        <div class="advert_content">
            <!-- 广告页advertpage.vue -->
            <h1><font style="font-size:15px;">{{num}}后跳转自动跳转首页</font></h1>
            <button @click="tohome">跳过</button>
            <router-link to="/details">
                <img  src="../../assets/images/mi03.webp.jpg" alt="">
            </router-link>
            <h1 class="text animated bounce" >小米手机，你值得拥有</h1>
        </div>
    </div>
</template>

<script>

    export default {
         data(){
            return{
                num:3,
                timer:null
            }
        },
        methods:{
            tohome(){
                this.$router.push("/home");//跳转首页
            }
        },
         created(){
            //   倒计时
            this.timer = setInterval(()=>{
                if(this.num == 0){
                    // 跳转首页
                    this.$router.push("/home");//跳转首页
                }else{
                    this.num--;
                }
            },1000)

        },
        // 切换组件生周期钩子，销毁函数
        beforeDestroy(){
           

            // 当路由器切换时候，关闭定时器
            clearInterval(this.timer);
        },
       
    }
</script>

<style lang="scss" scoped>
.advert_content{
    position: relative;
    padding: 100px 0;
    h1{
        text-align: center;
    }
    button{
        position: absolute;
        top:126px;
        right: 18px;
        border: none;
        background-color: white;
    }
    .text{
        font-size: 18px;
    }
    img{
        border-radius: 20px;
    }
}
</style>